.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
body {
	background-color: #5cbbe7;
	padding: 20px;
	margin: 0;
}
h1,
h2,
p,
ul,
li {
	font-family: sans-serif;
}
ul.header li {
	display: inline;
	list-style-type: none;
	margin: 0;
}
ul.header {
	background-color: #111;
	padding: 0;
}
ul.header li a {
	color: #fff;
	font-weight: bold;
	text-decoration: none;
	padding: 20px;
	display: inline-block;
}
.content {
	background-color: #fff;
	padding: 20px;
}
.content h2 {
	padding: 0;
	margin: 0;
}
.content li {
	margin-bottom: 10px;
}

.about-para {
	/* display: contents;*/
	text-align: justify;
	font-size: larger;
	margin: 1% 20% 1% 20%;
}
